
.container {
    max-width: 1200px;
    margin: 40px auto;
    padding: 20px;
    text-align: center;
  }
  
  .heading {
    color: #2c3e50;
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .introduction {
    color: #34495e;
    font-size: 1.2rem;
    margin-bottom: 40px;
  }
  
  .features {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .feature {
    flex-basis: 30%;
    background-color: #ecf0f1;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  }
  
  .feature h2 {
    color: #2980b9;
    margin-bottom: 15px;
  }
  
  .feature p {
    color: #7f8c8d;
    margin-bottom: 20px;
  }
  
  .link {
    background-color: #3498db;
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .link:hover {
    background-color: #2980b9;
  }